::-webkit-scrollbar {
    width: 7px;
}

/* Track */
::-webkit-scrollbar-track {
    background: #F6F6F6;
}

/* Handle */
::-webkit-scrollbar-thumb {
    background: rgb(126, 126, 126);
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
    background: rgb(25, 118, 210);
}
